<!-- 选项栏 -->
<template>
  <view class="cate">
    <view class="text_item">
      <view
        class="cate_item"
        v-for="(item, index) in titles"
        :class="{ active: index === currentIndex }"
        @click="itemClick(index)"
        >{{ item }}</view>
    </view>
	</view>
</template>

<script>
export default {
  name: "Cate",
  data() {
    return {
      titles: ["关注", "推荐"],
      currentIndex: 0,
    };
  },
  methods: {
    itemClick(index) {
      this.currentIndex = index;
      this.$emit("changeType", index);
    },
  },
};
</script>
<style scoped>
.cate {
	color: #333333;
  font-size: 16px;
  font-weight: 700;
  height: 80rpx;
  display: flex;
}
.text_item {
  display: flex;
  height: 80rpx;
  flex: 5;
}
.img_cate {
  flex: 2;
}
.cate_item {
  margin: auto;
  line-height: 24px;
  flex: 1;
  text-align: center;
}
.active {
  color: #f4912f;
  border-bottom: #eba182 6rpx solid;
  font-size: 40rpx;
  font-weight: 600;
  color: #111111;
}
</style>
